html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-KS);
  background-color: var(--bg_blue);
  background-image: url("/images/bg-pattern-top.svg"), url("/images/bg-pattern-bottom.svg");
  color: var(--desaturated_blue);
  min-height: 100vh;
  overflow: hidden;
  background-repeat: no-repeat, no-repeat;
  background-position: right 20vw bottom 60vh, left 25vw top 50vh;
}
@media (min-width: 40em) {
  body {
    background-image: url("/images/bg-pattern-top.svg"), url("/images/bg-pattern-bottom.svg");
    min-height: 100vh;
    overflow: hidden;
    background-repeat: no-repeat, no-repeat;
    background-position: -55% 195%, 140% -155%;
  }
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --bg_blue: hsl(185, 75%, 39%);
  --desaturated_blue: hsl(229, 23%, 23%);
  --dark_grayish_blue: hsl(227, 10%, 46%);
  --dark_gray: hsl(0, 0%, 59%);
}

:root {
  --font-KS:"Kumbh Sans", sans-serif;
}

h1,
h3 {
  font-weight: 700;
  margin-top: 0;
  font-size: 1.5625rem;
  color: var(--desaturated_blue);
}

h2 {
  font-weight: 700;
  margin: 0;
  margin-bottom: 0.3125rem;
  font-size: 1.25rem;
  color: var(--desaturated_blue);
  letter-spacing: normal;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.card {
  height: 98vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card_components {
  height: 26.25rem;
  width: 25.625rem;
  background-color: white;
  border-radius: 1.25rem;
  background-image: url("/images/bg-pattern-card.svg");
  background-repeat: no-repeat;
  background-size: 100% 9.375rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 39.9375em) {
  .card_components {
    width: 90%;
  }
}

.profile {
  height: 13.125rem;
  width: 25.9375rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.profile img {
  border: 0.3125rem solid white;
  border-radius: 100%;
}

.name_info {
  display: flex;
  align-items: baseline;
  justify-content: center;
  gap: 0.625rem;
  font-weight: 400;
  font-size: 1.5625rem;
  color: var(--dark_grayish_blue);
  margin-top: 1.25rem;
}

.place {
  color: var(--dark_grayish_blue);
  font-size: 1.125rem;
}

hr {
  color: rgba(150, 150, 150, 0.2784313725);
  width: 100%;
  margin-top: 1.5625rem;
}

.card_footer {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: auto;
  margin-top: 0.625rem;
  font-size: 0.75rem;
}

.followers {
  color: var(--dark_grayish_blue);
  letter-spacing: 0.125rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.likes {
  color: var(--dark_grayish_blue);
  letter-spacing: 0.0625rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.photos {
  color: var(--dark_grayish_blue);
  letter-spacing: 0.0625rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}